import { useMemo } from 'react';
import { useSchemaRegistry } from '../providers';
import { ActionsSDK } from '../utils/actions/types';
import { useBaseSDK } from './useBaseSDK';
import { useOptimisticActionsContext } from '../providers/OptimisticActionsContext';

export interface ActionsSDKParams {
  collectionId: string;
}

export const useActionsSDK = ({ collectionId }: ActionsSDKParams) => {
  const { getSchema } = useSchemaRegistry();

  const { getOptimisticActions } = useOptimisticActionsContext();

  const baseSDK = useBaseSDK();
  /*  eslint-disable react-hooks/exhaustive-deps */
  // disabled because bug in Patterns that navigateToEntityPage isn't wrapped with useCallback,
  const sdk = useMemo<ActionsSDK>(
    () => ({
      ...baseSDK, // TODO: Why spread and not sdk.app/base.openModal ... instead
      getSchema,
      collectionId,
      getOptimisticActions,
    }),
    [getSchema, collectionId, getOptimisticActions, baseSDK],
  );

  /*  eslint-enable react-hooks/exhaustive-deps */

  return sdk;
};
